var consulta = {"bytecode": "","interface":"[{\"constant\":true,\"inputs\":[],\"name\":\"profissional\",\"outputs\":[{\"name\":\"\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"evolucoes\",\"outputs\":[{\"name\":\"\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"med\",\"type\":\"string\"},{\"name\":\"pol\",\"type\":\"string\"},{\"name\":\"tempo\",\"type\":\"int256\"}],\"name\":\"solicitaMedicamento\",\"outputs\":[],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"periodoAfastamento\",\"type\":\"string\"},{\"name\":\"justificativa\",\"type\":\"string\"},{\"name\":\"cid\",\"type\":\"string\"}],\"name\":\"solicitaRepouso\",\"outputs\":[],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"fimPermanencia\",\"outputs\":[{\"name\":\"\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"id\",\"outputs\":[{\"name\":\"\",\"type\":\"int256\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"inicioPermanencia\",\"outputs\":[{\"name\":\"\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"anotacoes\",\"type\":\"string\"}],\"name\":\"registraConsulta\",\"outputs\":[],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":false,\"inputs\":[{\"name\":\"nome\",\"type\":\"string\"},{\"name\":\"obs\",\"type\":\"string\"},{\"name\":\"ident\",\"type\":\"int256\"}],\"name\":\"solicitaExame\",\"outputs\":[],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"function\"},{\"constant\":true,\"inputs\":[],\"name\":\"paciente\",\"outputs\":[{\"name\":\"\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"view\",\"type\":\"function\"},{\"inputs\":[{\"name\":\"pac\",\"type\":\"string\"},{\"name\":\"prof\",\"type\":\"string\"},{\"name\":\"idCon\",\"type\":\"int256\"},{\"name\":\"inicio\",\"type\":\"string\"},{\"name\":\"fim\",\"type\":\"string\"}],\"payable\":false,\"stateMutability\":\"nonpayable\",\"type\":\"constructor\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":false,\"name\":\"medicamento\",\"type\":\"string\"},{\"indexed\":false,\"name\":\"posologia\",\"type\":\"string\"},{\"indexed\":false,\"name\":\"prazo\",\"type\":\"int256\"}],\"name\":\"Instrucoes\",\"type\":\"event\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":false,\"name\":\"nomeExame\",\"type\":\"string\"},{\"indexed\":false,\"name\":\"observacao\",\"type\":\"string\"},{\"indexed\":false,\"name\":\"idExame\",\"type\":\"int256\"}],\"name\":\"Exame\",\"type\":\"event\"},{\"anonymous\":false,\"inputs\":[{\"indexed\":false,\"name\":\"periodoAfastamento\",\"type\":\"string\"},{\"indexed\":false,\"name\":\"justificativa\",\"type\":\"string\"},{\"indexed\":false,\"name\":\"cid\",\"type\":\"string\"}],\"name\":\"Repouso\",\"type\":\"event\"}]"}

// web3 provider
// const provider = new Web3.providers.HttpProvider('https://rinkeby.infura.io/v3/8b06d0b7817b4271850f52f13db61eb8')
const provider = new HDWalletProvider(
  'pave team season brush juice young alone away decorate high choose magic',
  'https://rinkeby.infura.io/v3/8b06d0b7817b4271850f52f13db61eb8'
)
var web3 = new Web3(provider)

// getting account
var accounts = {}
web3.eth.getAccounts().then(response => {
  console.log(response);
  accounts = response
})

// getting the contract
var contractAddress = "0x2413F7418D7d8fB015c548C9fAc28ADeEeCBc4fc"
var contract = new web3.eth.Contract(JSON.parse(consulta.interface), contractAddress)

console.log(web3.version)

export default {
    name: 'TestingEthereum',
    data () {
        return {
            paciente: "",
            profissional: "",
            inicioPermanencia: "",
            fimPermanencia: "",
            evolucoes: "",
            contratoAConsultar: "0x2413F7418D7d8fB015c548C9fAc28ADeEeCBc4fc"
        }
    },
    methods: {
        consultarContrato() {
            console.log("Consultando: " + this.contratoAConsultar)
            var contract = new web3.eth.Contract(JSON.parse(consulta.interface), this.contratoAConsultar)
            contract.methods.paciente().call().then(response => {
                this.paciente = response
            })
            contract.methods.profissional().call().then(response => {
                this.profissional = response
            })
            contract.methods.inicioPermanencia().call().then(response => {
                this.inicioPermanencia = response
            })
            contract.methods.fimPermanencia().call().then(response => {
                this.fimPermanencia = response
            })
            contract.methods.evolucoes().call().then(response => {
                this.evolucoes = response
            })
        }
    }
}
</script>

